@font-face {
    font-family: "GothamBook-Regular";
    src: url("/public/font/GothamBook-Regular.otf"); }

@font-face {
    font-family: "Gotham-Bold";
    src: url("/public/font/Gotham-Bold.ttf"); }

@font-face {
    font-family: "Gotham-Medium";
    src: url("/public/font/Gotham-Medium.ttf"); }

@font-face {
    font-family: "MyriadPro-Regular";
    src: url("/public/font/MyriadPro-Regular.otf"); }

@font-face {
    font-family: "OpenSans-Bold";
    src: url("/public/font/OpenSans-Bold.ttf"); }

@font-face {
    font-family: "OpenSans-Regular";
    src: url("/public/font/OpenSans-Regular.ttf"); }

@font-face {
    font-family: "RobotoCondensed-Bold";
    src: url("/public/font/RobotoCondensed-Bold.ttf"); }

@font-face {
    font-family: "Roboto-Regular";
    src: url("/public/font/Roboto-Regular.ttf"); }

$font-text: GothamBook-Regular, sans-serif;
$font-main-headline: Gotham-Bold, sans-serif;
$font-headlines: Gotham-Medium, sans-serif;
$font-links-navigation: MyriadPro-Regular, sans-serif;
$font-logo: OpenSans-Bold, sans-serif;
$font-scroll: OpenSans-Regular, sans-serif;
$font-footer: Roboto-Regular, sans-serif;
$font-bgS: RobotoCondensed-Bold, sans-serif;

body {
    margin: 0 auto;
    font-size: 15px;
    background-color: #fff; }
.about-agency {
    background-color: #11749e; }

.about-agency__preview {
    color: #fff;
    background-color: #2e85aa;
    border-radius: 50%;
    display: grid;
    justify-items: center;
    align-items: center; }

.area-card__preview {
    width: 100%; }

.area-card:nth-child(1) .area-card__preview {
    background: url("/public/media/img/area_1.png") no-repeat center;
    background-size: cover; }

.area-card:nth-child(2) .area-card__preview {
    background: url("/public/media/img/area_2.png") no-repeat center;
    background-size: cover; }

.area-card:nth-child(3) .area-card__preview {
    background: url("/public/media/img/area_3.png") no-repeat center;
    background-size: cover; }

.area-card:nth-child(4) .area-card__preview {
    background: url("/public/media/img/area_1.png") no-repeat center;
    background-size: cover; }

.bgS {
    font-family: $font-bgS;
    font-weight: 400;
    line-height: 210.64px;
    text-transform: uppercase;
    opacity: 0.18; }
.block-blog {
    padding: 10.5% 13.5% 5.5%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2.813vw auto;
    row-gap: 12vw; }

.block-blog__bgS {
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 1;
    grid-row-end: 2;
    align-self: center;
    justify-self: center; }

.block-blog__headline {
    z-index: 1;
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 2;
    justify-self: center; }

.block-blog__span {
    font-family: $font-text;
    font-size: 2vw;
    font-weight: 400;
    text-align: center;
    padding: 9vw;
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 2; }


.block-blog__card-container {
    display: grid;
    grid-auto-rows: auto;
    gap: 36px; }

.block-blog__card {
    display: grid;
    row-gap: 1.3vw;
    justify-items: center; }

.card-team__man-block {
    background-color: #fff;
    text-decoration: none;
    display: grid;
    align-items: center; }

.card-team__img {
    width: 17vw; }

.card-team__name {
    line-height: 24px; }

.card-team__spec {
    color: #000; }
.contact-area {
    background: url(/public/media/img/bg.png) no-repeat center;
    background-size: cover; }
.footer {
    display: grid;
    justify-items: center;
    align-items: center;
    background: #222; }

.footer__span {
    color: #fff;
    font-family: $font-footer;
    line-height: 36px;
    letter-spacing: 0.26px; }
.head-block {
    display: grid;
    height: 0px;
    padding-bottom: 56.25%;
    background: url(/public/media/img/bg.jpg) no-repeat center;
    background-size: cover; }

.head-block__container {
    display: grid;
    row-gap: 5vw; }

.head-block__main-headline {
    font-size: 10.2vw;
    font-family: $font-main-headline;
    color: #f5f5f5;
    margin: 0;
    text-transform: uppercase;
    text-align: center; }

.head-block__scroll-down {
    display: grid;
    justify-items: center;
    gap: 20%; }

.head-block__scroll-text {
    color: #fff;
    font-family: $font-scroll;
    font-weight: 400;
    line-height: 22px; }

.head-block__scroll-button {
    color: #f5f5f5;
    text-decoration: none;
    background: none;
    display: grid;
    align-items: center;
    justify-items: center;
    border-radius: 50%;
    border: 1px solid #fff; }

.form {
    display: grid;
    column-gap: 1.7vw;
    row-gap: 2.5vw; }

.form__input {
    color: #fff;
    font-family: $font-text;
    background: rgba(0, 0, 0, 0);
    border: 1px solid #fff; }

.form__message {
    resize: none; }

.form__button {
    background-color: #11749e;
    border: none;
    justify-self: center; }

.form__input::placeholder {
    color: #fff;
    font-family: $font-text;
    letter-spacing: 0.3px; }

.form__button:hover,
.form__button:focus {
    outline: none;
    cursor: pointer; }


.form__input:hover,
.form__input:focus {
    outline: none; }

.form__input::-webkit-outer-spin-button,
.form__input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }
.header {
    display: grid;
    justify-items: center;
    align-items: center; }
.headline {
    font-family: $font-headlines;
    font-weight: 500;
    text-transform: uppercase;
    margin: 0; }

.logo__container {
    position: relative; }

.logo__container-front {
    background: #11749e;
    display: grid;
    justify-items: center;
    align-items: center;
    z-index: 1; }

.logo__container-back {
    background: #fff;
    z-index: 0;
    opacity: .75; }

.logo__link {
    color: #f5f5f5;
    font-family: $font-logo;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: 700; }


.map {
    width: 100%;
    height: 21vw; }
.navigation {
    width: 100%;
    display: grid;
    justify-items: center; }

.navigation__link {
    color: #fff;
    font-family: $font-links-navigation;
    text-decoration: none;
    font-weight: 400; }


.news-area {
    display: grid; }

.news-area__img-link {
    background-size: cover; }

.news-area__info {
    display: grid; }

.news-area__data {
    align-self: center;
    text-align: left; }

.news-area__text {
    text-align: left; }
.quick-action-area {
    background: #11749e;
    display: grid;
    grid-auto-rows: auto;
    gap: 4vw; }

.quick-action-area__card {
    display: grid;
    grid-template-columns: 1fr 6.5fr;
    grid-template-rows: 1fr;
    column-gap: 2.4vw;
    justify-items: center;
    align-items: center; }

.quick-action-area__icon {
    color: #fff; }

.quick-action-area__text {
    text-align: left; }

.shadow:hover {
    outline: none;
    cursor: pointer; }

.shadow:focus {
    outline: none; }
.social-icons {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(40px, auto));
    justify-items: center;
    justify-self: start; }

.social-icons__icon {
    color: #f5f5f5; }

.text {
    font-family: $font-text;
    font-weight: 400;
    margin: 0;
    text-align: center; }
.bgS_dark {
    color: #222; }
.bgS_ligth {
    color: #fff; }
.headline_dark {
    color: #222; }
.headline_blue {
    color: #11749e; }
.headline_ligth {
    color: #fff; }
.news-area__img-link_reverse {
    background: url("/public/media/img/news_1.png") no-repeat center;
    background-size: cover; }

.news-area__img-link_straight {
    background: url("/public/media/img/news_2.png") no-repeat center;
    background-size: cover; }
.news-area__info_straight {
    background: #222; }

.shadow_box_black-inset:hover {
    box-shadow: inset 0 0 20px #000; }
.shadow_box_black-outset:hover {
    box-shadow: 0 0 20px #000; }
.shadow_box_white-inset:hover {
    box-shadow: inset 0 0 20px #fff; }
.shadow_box_white-outset:hover {
    box-shadow: 0 0 20px #fff; }
.shadow_text_black-inset:hover {
    text-shadow: inset 0 0 20px #000; }
.shadow_text_black-outset:hover {
    text-shadow: 0 0 20px #000; }
.shadow_text_white-inset:hover {
    text-shadow: inset 0 0 20px #fff; }
.shadow_text_white-outset:hover {
    text-shadow: 0 0 20px #fff; }
.text_dark {
    color: #222; }
.text_ligth {
    color: #fff; }
